.overlay {
    position: fixed;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(12px);
    z-index: var(--z-modal);
    padding: var(--space-4);
}

.modal {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: var(--space-12) var(--space-10);
    background: linear-gradient(135deg,
            rgba(139, 92, 246, 0.15) 0%,
            rgba(236, 72, 153, 0.1) 50%,
            rgba(6, 182, 212, 0.05) 100%);
    border: 1px solid rgba(139, 92, 246, 0.4);
    border-radius: var(--radius-2xl);
    text-align: center;
    overflow: hidden;
    max-width: 400px;
    width: 100%;
}

.glow {
    position: absolute;
    inset: -100px;
    background: radial-gradient(circle at center,
            rgba(139, 92, 246, 0.4) 0%,
            transparent 60%);
    animation: glowPulse 3s ease-in-out infinite;
}

@keyframes glowPulse {

    0%,
    100% {
        opacity: 0.5;
        transform: scale(1);
    }

    50% {
        opacity: 1;
        transform: scale(1.1);
    }
}

.star {
    position: absolute;
    top: -20px;
    font-size: 6rem;
    opacity: 0.1;
}

.levelBadge {
    width: 100px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, var(--primary-500), var(--accent-pink));
    border-radius: 50%;
    margin-bottom: var(--space-6);
    box-shadow:
        0 0 60px rgba(139, 92, 246, 0.6),
        0 0 120px rgba(236, 72, 153, 0.3);
    position: relative;
    z-index: 1;
}

.levelBadge::before {
    content: '';
    position: absolute;
    inset: -4px;
    background: linear-gradient(135deg, var(--primary-400), var(--accent-pink), var(--accent-cyan));
    border-radius: 50%;
    z-index: -1;
    animation: borderRotate 3s linear infinite;
}

@keyframes borderRotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.levelNumber {
    font-size: 2.5rem;
    font-weight: 900;
    font-family: var(--font-display);
    color: white;
    text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
}

.title {
    font-size: 2.5rem;
    font-weight: 900;
    font-family: var(--font-display);
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    margin: 0 0 var(--space-2) 0;
    position: relative;
    z-index: 1;
}

.subtitle {
    font-size: 1.125rem;
    color: var(--text-secondary);
    margin: 0 0 var(--space-2) 0;
    position: relative;
    z-index: 1;
}

.highlight {
    color: var(--accent-pink);
    font-weight: 600;
}

.message {
    font-size: 0.9375rem;
    color: var(--text-muted);
    margin: 0 0 var(--space-6) 0;
    position: relative;
    z-index: 1;
}